<script>
export default {
    data() {
        return {
            typedText: '',
            text: ['Hi!', 'Nice to meet u!'],
            index: 0,
            typingSpeed: 50,
            deletingSpeed: 20,
            showCaret: false,
            userName: '',
        }
    },
    mounted() {
        this.type();
    },
    methods: {
        type() {
            setTimeout(() => {
                if (this.index >= this.text.length) {
                    this.index = 0;
                }
                this.typedText = this.text[this.index].slice(0, this.typedText.length + 1);
                this.showCaret = false;
                if (this.typedText.length === this.text[this.index].length) {
                    setTimeout(() => {
                        this.delete();
                    }, 1000);
                } else {
                    this.showCaret = true;
                    this.type();
                }
            }, this.typingSpeed);
        },
        delete() {
            setTimeout(() => {
                this.showCaret = false;
                this.typedText = this.typedText.slice(0, this.typedText.length - 1);
                if (this.typedText.length === 0) {
                    this.index++;
                    this.type();
                } else {
                    this.delete();
                }
            }, this.deletingSpeed);
        },
    },
}
</script>


